import React, { Component } from "react";
import anime from "animejs";
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

import styles from "./About.css";
import bgColors from "../../const/bgColors";

library.add(faTimes);

class About extends Component<any, {}> {
  // sp
  spOpenDetail = () => {
    const openDetailTimeline = anime.timeline();
    openDetailTimeline
      .add({
        targets: "#detailContainer",
        zIndex: 30,
        duration: 10
      })
      .add({
        targets: "#detailContainer",
        opacity: 0.9,
        easing: "easeInOutSine",
        duration: 500
      })
      .add({
        targets: "#detailContainer div",
        opacity: [0, 0.9],
        easing: "easeInOutSine",
        delay: function(el, index) {
          return index * 50;
        }
      });
  };
  // sp
  spCloseDetail = () => {
    const openDetailTimeline = anime.timeline();
    openDetailTimeline
      .add({
        targets: "#detailContainer",
        opacity: 0,
        easing: "easeInOutSine"
      })
      .add({
        targets: "#detailContainer",
        zIndex: 0
      });
  };
  render() {
    return (
      <div className={styles.container}>
        {bgColors.map(bgColor => {
          return (
            <div
              className={styles.background}
              style={{ background: bgColor }}
              key={bgColor}
            />
          );
        })}
        <div className={styles.title}>
          <div className={styles.titleFont}>ABOUT</div>
          <div className={styles.spButton} onClick={this.spOpenDetail}>
            more
          </div>
        </div>
        <div className={styles.detailContainer} id="detailContainer">
          <div className={styles.detail}>
            <div onClick={this.spCloseDetail}>
              <FontAwesomeIcon icon="times" className={styles.closeIcon} />
            </div>
            <div className={styles.detailTitle}> 24</div>
            <div className={styles.detailText}>
              Web
              <div className={styles.darkFont}>
                HTML / CSS
                <br />
                React / Redux / TypeScript
                <br />
                CakePHP / Laravel / MySQL
                <br />
                Illustrator / Photoshop / AfterEffect
                <br />
                Git / Fish / VSCode
                <br />
              </div>
            </div>
            <div className={styles.detailText}>
              
              <br />
              <a href="https://github.com/hiraike32" target="_blank">
                Github
              </a>
            </div>
            <div className={styles.detailText}>
              
              <div className={styles.darkFont}>
                <a
                  href="https://www.photo-ac.com/profile/1000333"
                  target="_blank"
                >
                  AC
                </a>
                
                <div className={styles.smallFont}>
                  6,500
                </div>
              </div>
            </div>
            <div className={styles.detailText}>
              
              <div className={styles.darkFont}>
                
                <div className={styles.smallFont}>15859</div>
              </div>
            </div>
            <div className={styles.detailText}>
              
              <div className={styles.darkFont}>
                <div className={styles.smallFont}>20188~</div>
                 / 
                <div className={styles.smallFont}>20186~</div>
                Smiloops / 
                <div className={styles.smallFont}>20179~</div>
                 / 
                <div className={styles.smallFont}>201612~</div>
                 / 
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
